@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);

body {
  font-family: "Montserrat", sans-serif;
}

.section-padding {
  padding: 0 10%;
}

.header a {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}
.header .top-banner {
  font-size: 14px;
  background: #FFF100;
  box-shadow: 0px 0.73px 2px rgba(0, 0, 0, 0.5019607843);
}
.header .bottom-banner {
  background: #000;
}
.header .bottom-banner a {
  color: #fff;
}
.header .header-cart {
  position: relative;
}
.header .header-cart .counter {
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  top: 0;
  right: 0;
  background: #FFF100;
  font-size: 10px;
  color: #3c3c3c;
  text-align: center;
}

.input-container {
  padding: 9px;
  border: 1px solid #D8D8D8;
  border-radius: 24px;
}
.input-container input {
  margin-left: 5px;
  width: 90%;
  border: none;
}
.input-container input:focus {
  outline: none;
}

.bottom-border {
  box-shadow: 0px 0.73px 2px rgba(0, 0, 0, 0.5019607843);
}

.rec-carousel .carousel-arrows {
  margin-top: 40px;
}
.rec-carousel img {
  height: 120px;
  width: 120px;
}

.product-details .brand-img {
  width: 80px;
  height: auto;
}
.product-details h4, .product-details h5, .product-details h6 {
  color: #000;
}
.product-details input[type=radio] {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.product-details input[type=radio] + span {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 1px solid #D9D9D9;
  margin: 0 7px;
  font-size: 12px;
  color: #542E90;
}
.product-details input[type=radio]:checked + span {
  color: #000;
  background: #ECECEC;
  font-weight: bold;
}
.product-details input[type=radio] + img {
  cursor: pointer;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin: 0 7px;
}
.product-details input[type=radio]:checked + img {
  border: 1px solid #542E90;
}
.product-details .input-container {
  display: inline-block;
  padding: 5px;
  background-color: #F5F5F5;
  border: 1px solid #D9D9D9;
}
.product-details .input-container input {
  width: 150px;
  background: #F5F5F5;
  text-align: center;
}
.product-details .input-container button {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: #FFF100;
  border: none;
}
.product-details input::-webkit-outer-spin-button,
.product-details input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.section-title {
  font-size: 28px;
  color: #542E90;
}

.highlightLabel {
  padding: 3px 10px;
  background: #FFF200;
  border-radius: 3px;
  color: #000;
  font-weight: bold;
}

.button {
  font-weight: bold;
  font-size: 16px;
  width: 200px;
  padding: 13px 0;
  text-align: center;
  border-radius: 27px;
  border: none;
  transition: all ease-in-out 0.4s;
}
.button-main {
  color: #fff;
  background-color: #542E90;
}
.button-main:hover {
  background-color: #3d2269;
}
.button-secondary {
  color: #000;
  background-color: #FFF100;
}
.button-secondary:hover {
  background-color: #faec00;
}

.shopping-cart-container {
  position: fixed;
  top: 0;
  right: 0;
  min-height: 100vh;
  width: 400px;
  z-index: 9;
  background-color: #fff;
  box-shadow: -1.78px -1.78px 4px rgba(0, 0, 0, 0.2509803922);
}
.shopping-cart-container .card {
  border: 0.75px solid #ECECEC;
  border-radius: 7px;
}
.shopping-cart-container .card p {
  font-size: 14px;
  margin-bottom: 0;
}
.shopping-cart-container .card img {
  width: 100%;
  height: auto;
}
.shopping-cart-container .card .button {
  font-size: 10px;
  padding: 5px;
  width: 80px;
}
.shopping-cart-container .button {
  width: 100%;
  font-size: 14px;
}

.overlay {
  position: fixed;
  z-index: 5;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3);
}

footer {
  background: #3C3C3B;
  font-size: 16px;
  color: #fff;
}
footer .input-container {
  padding: 4px;
  background: #fff;
}
footer .input-container input {
  margin-left: 0;
  width: 70%;
  padding: 0 6px;
}
footer .input-container button {
  width: 30%;
}
footer ul {
  list-style-type: none;
  padding: 0;
}
footer ul li {
  padding-bottom: 12px;
}
footer ul li a {
  color: #fff;
  text-decoration: none;
}
footer ul li a:hover {
  color: #fff;
}
footer ul li .social-logo {
  width: 33px;
  height: 33px;
  margin-right: 10px;
}

.primary-color {
  color: #542E90;
}

.rating-star {
  font-size: 1.5rem;
  color: #FFB900;
}
